<svelte:options tag="event-inquiry-form" />

<script>
    import { onMount } from "svelte"
    import { createMachine, interpret } from "xstate"
    import { http } from "../utils/http"

    $: inquiry = false
    $: data = {
        name: null,
        company: null,
        email: null,
        phone: null,
        type: "",
        date: null,
        locations: [],
        amount_guests: "",
        catering_needed: "",
        technic_needed: "",
        text: null,
    }
    $: collapse = (key) => {
        let hidden = true

        switch (key) {
            case "type":
                hidden = false
                break
            case "amount_guests":
                hidden = !data.type
                break
            case "date":
                hidden = !(data.type && data.amount_guests)
                break
            case "locations":
                hidden = !(data.type && data.amount_guests && data.date)
                break
            case "catering_needed":
                hidden = !(
                    data.type &&
                    data.amount_guests &&
                    data.date &&
                    data.locations.length
                )
                break
            case "technic_needed":
                hidden = !(
                    data.type &&
                    data.amount_guests &&
                    data.date &&
                    data.locations.length &&
                    data.catering_needed
                )
                break
            case "contact":
                hidden = !(
                    data.type &&
                    data.amount_guests &&
                    data.date &&
                    data.locations.length
                )
                break
        }

        return hidden
    }
    $: autosave = () => {
        localStorage.setItem("event-inquiry-autosave", JSON.stringify(data))
    }

    let apiState = false
    let formState = false

    const apiMachine = createMachine({
        id: "api",
        initial: "inactive",
        states: {
            inactive: {
                on: {
                    SUBMIT: { target: "loading" },
                },
            },
            loading: {
                on: {
                    RESOLVE: { target: "success" },
                    REJECT: { target: "error" },
                },
            },
            success: {},
            error: {
                on: {
                    SUBMIT: { target: "loading" },
                },
            },
        },
    })

    const apiService = interpret(apiMachine)
        .onTransition((state) => (apiState = state))
        .start()

    const formMachine = createMachine({
        id: "form",
        initial: "new",
        states: {
            new: {
                on: {
                    NEXT: { target: "finished" },
                    SKIP: { target: "finished" },
                },
            },
            finished: {
                on: {
                    RESET: { target: "new" },
                },
            },
        },
    })

    const formService = interpret(formMachine)
        .onTransition((state) => (formState = state))
        .start()

    onMount(() => {
        const autosave = JSON.parse(
            localStorage.getItem("event-inquiry-autosave")
        )
        if (autosave) {
            data = autosave
        } else {
            const storedInquiry = JSON.parse(
                localStorage.getItem("event-inquiry")
            )
            if (storedInquiry) {
                let createdAt = new Date(storedInquiry.created_at)
                createdAt = createdAt.toISOString().split("T")[0]
                let threshold = new Date()
                threshold.setTime(threshold.getTime() - 2629800000)
                threshold = threshold.toISOString().split("T")[0]
                if (threshold > createdAt) {
                    localStorage.removeItem("event-inquiry")
                } else {
                    inquiry = storedInquiry
                    formService.send("SKIP")
                }
            }
        }
    })

    async function submit() {
        try {
            apiService.send("SUBMIT")
            let temp = { ...data }

            temp.catering_needed =
                data.catering_needed === "true" ? true : false
            temp.technic_needed = data.technic_needed === "true" ? true : false
            if (!temp.company) delete temp.company
            if (!temp.text) delete temp.text

            const response = await http
                .post("event-inquiries/submit", {
                    json: temp,
                })
                .json()

            localStorage.removeItem("autosave")
            localStorage.setItem("event-inquiry", JSON.stringify(response))
            inquiry = response
            apiService.send("RESOLVE")
            formService.send("NEXT")
        } catch (err) {
            console.log(err)
            apiService.send("REJECT")
        }
    }

    function reset() {
        data = {
            name: null,
            company: null,
            email: null,
            phone: null,
            type: "",
            date: null,
            locations: [],
            amount_guests: "",
            catering_needed: "",
            technic_needed: "",
            text: null,
        }
        formService.send("RESET")
    }

    function locationSelect() {
        if (this.checked) {
            data.locations = [...data.locations, Number(this.dataset.id)]
        } else {
            data.locations = data.locations.filter(
                (location) => location !== Number(this.dataset.id)
            )
        }
    }
</script>

<link
    href="https://fonts.googleapis.com/css?family=Montserrat"
    rel="stylesheet"
/>
<link
