                </div>
                <div class="attribute">
                    <div class="label"><b>Name:</b></div>
                    <div class="value">{inquiry.name}</div>
                </div>
                {#if inquiry.company}
                    <div class="attribute">
                        <div class="label"><b>Firma:</b></div>
                        <div class="value">{inquiry.company}</div>
                    </div>
                {/if}
                <div class="attribute">
