                    </div>
                </div>
                <div class="attribute">
                    <div class="label"><b>Technik:</b></div>
                    <div class="value">
                        {inquiry.technic_needed ? "Ja" : "Nein"}
                    </div>
                </div>
                {#if inquiry.text}
